'use client';
import './globals.css';
import Link from 'next/link';
import { ThemeProvider } from '../components/ThemeProvider';
import { HeroUIProvider } from '@heroui/react';
import { Sun, Moon } from 'lucide-react';
import { useTheme } from 'next-themes';

export const metadata = {
  title: 'AI Meeting Summarizer',
  description: 'Upload, search, and summarize your meetings with AI.'
};

function ThemeToggle() {
  const { theme, setTheme } = useTheme();
  return (
    <button
      aria-label="Toggle Dark Mode"
      className="rounded-full p-2 bg-zinc-800 hover:bg-zinc-700 transition-colors border border-zinc-700 ml-4"
      onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
    >
      {theme === 'dark' ? <Sun className="w-5 h-5 text-yellow-300" /> : <Moon className="w-5 h-5 text-zinc-300" />}
    </button>
  );
}

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body className="bg-[--color-bg] text-[--color-text] min-h-screen">
        <ThemeProvider attribute="class" defaultTheme="dark" enableSystem>
          <HeroUIProvider>
            <header className="sticky top-0 z-50 w-full bg-gradient-to-r from-zinc-900 to-zinc-800 shadow-lg border-b border-zinc-700">
              <nav className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
                <Link href="/" className="text-2xl font-extrabold bg-gradient-to-r from-indigo-400 to-fuchsia-500 bg-clip-text text-transparent tracking-tight">
                  AI Meeting Summarizer
                </Link>
                <div className="flex items-center space-x-6">
                  <Link href="/" className="text-zinc-200 hover:text-fuchsia-400 font-semibold transition-colors">Home</Link>
                  <Link href="/results/test-job" className="text-zinc-200 hover:text-fuchsia-400 font-semibold transition-colors">Sample Results</Link>
                  <ThemeToggle />
                </div>
              </nav>
            </header>
            <main className="flex-1 flex flex-col items-center justify-center px-4">
              {children}
            </main>
            <footer className="w-full bg-zinc-900 border-t border-zinc-800 mt-16 py-4 text-center text-zinc-400 text-sm">
              &copy; {new Date().getFullYear()} AI Meeting Summarizer. All rights reserved.
            </footer>
          </HeroUIProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}
